  boxClick (index) {
    this.index = index
    let backData = this.items[this.index]
    this.$emit('change', backData)
  }

  getCommodityClassList () {
    const toast = this['$createToast']({
      time: 0,
      txt: '...'
    })
    toast.show()
    this.$store.dispatch('getCommodityClassList', {}).then((response) => {
      if (response.success) {
        let list = response.data.list
        this.$set(this, 'items', list)
        this.boxClick(0)
      }
      toast.hide()
    })
  }
