






import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'
import banner from '@/components/common-components/bannerSlide'
import NP from 'number-precision'
@Component({
  components: {
    banner
  },
  filters: {
    getFormatDate (date: any) {
      return new Date(date).getFullYear() + '' + (new Date(date).getMonth() + 1) + '' + new Date(date).getDate()
    },
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '' + (num / 100).toFixed(2)
    },
    billType (type: any) {
      if (type === 'property') {
        return ''
      } else if (type === 'parking') {
        return ''
      } else if (type === 'water') {
        return ''
      } else {
        return ''
      }
    }
  }
})
export default class Home extends Vue {
  indicator = false
  $createDialog : any
  $createToast : any
  // 
  model = { phone: '', openid: '' }

  actions = [
    {
      text: ' 00.00',
      action: 'moreMoney'
    },
    {
      type: 'button',
      text: '<div class="payButton"></div>',
      action: 'payButtonClickHandle'
    }
  ]

  // 
  page = 1
  commodityId = ''
  commodityObj = {}
  commodityNum = 1
  totalCost = 0
  userInfo = {}
  openid = ''
  payButtonClick = false
  weixinInfo = {}
  activated () {
    this.$store.commit('SET_SESSION_ID', 'noSession')
    if (!this.$store.state.isUserInfoBack) {
      this.pageInit()
    } else {
      this.$set(this, 'userInfo', this.$store.state.commodityUserInfo)
      if (this.payButtonClick) {
        this.payButtonClickHandle() 
      }
    }
  }

  get selectCommodityList () {
    let allPrice = 0
    this.$store.state.selectCommodityList.forEach((item)=>{
      allPrice += item.price * item.num
    })
    return NP.strip(allPrice)
  }

  @Watch('selectCommodityList')
  selectCommodityListChanged (newPrice) {
    this.actions[0].text = ' ' + newPrice
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 
  get computedMsg () {
    return 'computed ' + this.propB
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
  }
  getUserInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '...'
      })
      toast.show()
      this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.getWeiXinConfig()
            if (response.data.userInfo) {
              this.userInfo = response.data.userInfo
            }
            if (response.data.weixinInfo) {
              this.weixinInfo = response.data.weixinInfo
            }
            // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          }
        }
        toast.hide()
      })
    } else {
      console.log('')
    }
  }
  pageInit () {
    let query = this.$route.query
    let state = query.state
    if (state) {
      let stateArr = state.split('@')
      if (stateArr[0] === 'commodity') {
        this.commodityId = stateArr[1].replace(/\'/g,"")
      }
    }
    if (query.code) {
      this.getUserInfo()
    }
  }
  getCommodityById () {
    const toast = this['$createToast']({
      time: 0,
      txt: '...'
    })
    toast.show()
    this.$store.dispatch('getCommodityById', { id: this.commodityId}).then((response) => {
      if (response.success) {
        this.$set(this, 'commodityObj', response.data)
        this.initTotalCost()
      }
      toast.hide()
    })
  }
  fenToYuan (num: any) {
    if (typeof num !== 'number' || isNaN(num)) return null
    return ' ' + NP.strip((num / 100).toFixed(2))
  }
  getWeiXinConfig () {
    // let url = window.location.origin + window.location.pathname
    let url = window.location.href
    this.$store.dispatch('getWeixinConfig', { url: url }).then((res)=>{
      console.log('', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: config.appId, // 
          timestamp: config.timestamp, // 
          nonceStr: config.nonceStr, // 
          signature: config.signature, // 
          jsApiList: ['chooseWXPay'] // JS
        })
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }
  clickHandler (item: any) {
    if (item.action) {
      this[item.action](item)
    }
  }

  payButtonClickHandle () {
    this.payButtonClick = true
    let commodityList = []
    this.$store.state.selectCommodityList.forEach((item) => {
      let obj = {}
      let fee = 0
      obj.id = item.id
      obj.name = item.name
      obj.num = item.num
      obj.price = item.price
      commodityList.push(obj)
    })
    if (commodityList.length <= 0) {
       const toast = this.$createToast({
        txt: '',
        type: 'error'
      })
      toast.show()
      return
    }
    if (!this.userInfo.phone || !this.userInfo.address) {
      this.$router.push({ path: '/orderConfirm', query: { userInfo: this.userInfo, openid: this.openid } })
    } else {
      let that = this
      let query = {}
      query.fee = this.selectCommodityList
      query.body = ''
      query.openid = this.openid
      // query.commodityId = this.commodityId
      query.commodityList = commodityList
      // query.num = this.commodityNum
      // query.subscribe = this.weixinInfo.subscribe
      this.$store.dispatch('setCommodityOrder', query).then((response) => {
        if (response.success) {
          let payData = response.data
          wx.chooseWXPay({
            timestamp: payData.timeStamp, // jssdktimestamptimeStampS
            nonceStr: payData.nonceStr, //  32 
            package: payData.package, // prepay_idprepay_id=\*\*\*
            signType: payData.signType, // 'SHA1''MD5'
            paySign: payData.paySign, // 
            success: function (res) {
              // 
              that.payButtonClick = false
              const toast = that.$createToast({
                time: 2000,
                txt: '...',
                mask: true,
                onTimeout: () => {
                  // that.getBillListByHouseId()
                  // that['$createDialog']({
                  //   type: 'alert',
                  //   content: '\n ',
                  //   icon: 'cubeic-ok'
                  // }).show()
                  that.$router.replace({ path: '/paySuccess', query: { openid: that.openid } })
                }
              })
              toast.show()
            },
            error: function () {

            }
          })
        }
      })
    }
  }
  userInfoClick () {
    this.$router.push({ path: '/orderConfirm', query: { userInfo: this.userInfo, openid: this.openid } })
  }
 
  tabBarChange (value:any) {
    this.page = 1
    this.isShowMore = true
    this.billList = []
    this.getBillListByHouseId()
  }
  showMoreClick () {
    this.page++
    this.getBillListByHouseId()
  }
  setHouseId (id:any) {
    if (this.houseId !== id) {
      this.houseId = id
      this.getWeiXinConfig()
      this.getBillListByHouseId()
    }
  }
}
